import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthorHomepageComponent } from './author-homepage/author-homepage.component';
import { MyInfoComponent } from './my-info/my-info.component';
import { SaleCountComponent } from './sale-count/sale-count.component';
import { QueryMoneyComponent } from './query-money/query-money.component';
import { ApplyConstractComponent } from './apply-constract/apply-constract.component';
import { ApplyGroudingComponent } from './apply-grouding/apply-grouding.component';
import { CommmentManagerComponent } from './commment-manager/commment-manager.component';
import { WorksDataComponent } from './works-data/works-data.component';
import { MoneyBillsComponent } from './money-bills/money-bills.component';
import { RewardDetailComponent } from './reward-detail/reward-detail.component';
import { GiveIncomeComponent } from './give-income/give-income.component';
import { LeftoversDetailComponent } from './leftovers-detail/leftovers-detail.component';
import { OrderCountComponent } from './order-count/order-count.component';
import { RedCountComponent } from './red-count/red-count.component';
import { ClickCountComponent } from './click-count/click-count.component';
import { UpdateCountComponent } from './update-count/update-count.component';





const appRoutes:Routes = [
	// {path:路径,component:组件}
	{path:"home",component:AuthorHomepageComponent},
	{path:"my-info",component:MyInfoComponent},
	{path:"sale-count",component:SaleCountComponent},
	{path:"query-money",component:QueryMoneyComponent,children:[
		{path:"money-bills",component:MoneyBillsComponent},
		{path:"reward-detail",component:RewardDetailComponent},
		{path:"give-income",component:GiveIncomeComponent},
		{path:"leftovers-detail",component:LeftoversDetailComponent}

	]},
	{path:"apply-constract",component:ApplyConstractComponent},
	{path:"apply-grouding",component:ApplyGroudingComponent},
	{path:"commment-manager",component:CommmentManagerComponent},
	{path:"works-data",component:WorksDataComponent,children:[
		{path:"order-count",component:OrderCountComponent},
		{path:"red-count",component:RedCountComponent},
		{path:"click-count",component:ClickCountComponent},
		{path:"update-count",component:UpdateCountComponent}
	]},

	// 重定向
	{path:"",redirectTo:"/home",pathMatch:"full"},
	{path:"**",component:AuthorHomepageComponent}
];
@NgModule({
	imports:[
		RouterModule.forRoot(appRoutes)
	],
	exports:[
		RouterModule
	]
})
export class AppRoutingModule{}


