import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-commment-manager',
  templateUrl: './commment-manager.component.html',
  styleUrls: ['../money-bills/money-bills.component.css','./commment-manager.component.css']
})
export class CommmentManagerComponent implements OnInit {
	dataList=[
		{id:1564,name:"《十里春风不如你》",count:465},
		{id:1564,name:"《猩球崛起》",count:465},
		{id:1564,name:"《蜘蛛侠》",count:465},
		{id:1564,name:"《复仇者联盟》",count:465},
		{id:1564,name:"《花火》",count:465},
	];
	deleteBook(index){
		if (confirm("确定要删除此书吗？")) {
			this.dataList.splice(index,1);
		}
	}
  constructor() { }

  ngOnInit() {
  }

}



