import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-sale-count',
  templateUrl: './sale-count.component.html',
  styleUrls: ['./sale-count.component.css']
})
export class SaleCountComponent implements OnInit {
	years = [
		{value:"2017",display:"2017"},
		{value:"2016",display:"2016"},
		{value:"2015",display:"2015"},
		{value:"2014",display:"2014"},
		{value:"2013",display:"2013"},
		{value:"2012",display:"2012"}
	];
	months = [];
	// 设置初始年、月
	year = new Date().getFullYear();
	month = new Date().getMonth()+1;

	// 获取年份
	getSelectYear(){
		console.log(this.year)

	}
	getSelectMonth(){
		console.log(this.month)
	}
	// echarts的配置数据
	chOPtion = {
		title:{
			text:this.year+"年"+this.month+"月销售统计"
		},
		tooltip:{
			trigger:"axis"
		},
		legend:{
			data:['围城',"史记","三国演义","水浒传"]
		},
		xAxis:[{
			type:"category",
			data:['第一周','第二周','第三周','第四周']
		}],
		yAxis:[
			{
				type:'value'
			}
		],
		series:[
			{
				name:"围城",
				type:"line",
				stack:"总量",
				data:[10,92,35,64]
			},{
				name:"史记",
				type:"line",
				stack:"总量",
				data:[90,92,35,64]
			},{
				name:"三国演义",
				type:"line",
				stack:"总量",
				data:[70,62,35,64]
			},{
				name:"水浒传",
				type:"line",
				stack:"总量",
				data:[100,112,135,164]
			}
		]
	}
	chOPtion1 = {
		title:{
			text:this.year+"年"+this.month+"月销售统计"
		},
		tooltip:{
			trigger:"axis"
		},
		legend:{
			data:['围城',"史记","三国演义","水浒传"]
		},
		xAxis:[{
			type:"category",
			data:['第一周','第二周','第三周','第四周']
		}],
		yAxis:[
			{
				type:'value'
			}
		],
		series:[
			{
				name:"围城",
				type:"bar",
				data:[10,92,35,64]
			},{
				name:"史记",
				type:"bar",
				data:[90,92,35,64]
			},{
				name:"三国演义",
				type:"bar",
				data:[70,62,35,64]
			},{
				name:"水浒传",
				type:"bar",
				data:[100,112,135,164]
			}
		]
	}
  constructor() { }

  ngOnInit() {
  	for (var i = 0; i < 12; ++i) {
		this.months[i] = {value:i+1,display:i+1};
	}
  }

}



